.editor-toolbar{padding:0px;border-bottom:1px solid #c2c2c2;z-index:1000;width:100%;height:56px;display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex}.editor-toolbar a{float:left;display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-webkit-justify-content:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-webkit-align-items:center;-ms-flex-align:center;align-items:center;width:56px;height:56px;cursor:pointer;color:#287bbb;font-weight:bold;text-transform:uppercase;font-size:13px;line-height:1}.editor-toolbar a:hover{background:#c9e3f4}.editor-toolbar .spacer{-webkit-box-flex:10;-webkit-flex-grow:10;-ms-flex-positive:10;flex-grow:10}



