.button,.newsletter-create__button{display:block;background:#287bbb;color:#fff;border:0;padding:8px 20px 8px;font-size:12px;font-weight:600;text-transform:uppercase;cursor:pointer;border-radius:5px;padding-top:18px;padding-bottom:15px;text-decoration:none;width:100%;outline:none}.button:disabled,.newsletter-create__button:disabled{background:#dddddd;cursor:default}.newsletter-create{position:relative;width:100%;display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex}.newsletter-create__button--opened{border-radius:0 5px 5px 0;width:30%}.newsletter-create__input{width:75%}.newsletter-create>*{-webkit-transition:width .25s;transition:width .25s}.newsletter-create>*.v-enter,.newsletter-create>*.v-leave{width:0}



