.button,.sections-add__button{display:block;background:#287bbb;color:#fff;border:0;padding:8px 20px 8px;font-size:12px;font-weight:600;text-transform:uppercase;cursor:pointer;border-radius:5px;padding-top:18px;padding-bottom:15px;text-decoration:none;width:100%;outline:none}.button:disabled,.sections-add__button:disabled{background:#dddddd;cursor:default}.sections-add{padding:30px;box-sizing:border-box;position:relative;width:100%;display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex}.sections-add__button--opened{border-radius:0 5px 5px 0;width:30%}.sections-add__input{width:75%;border-radius:5px 0 0 5px}.sections-add>*{-webkit-transition:width .25s;transition:width .25s}.sections-add>*.v-enter,.sections-add>*.v-leave{width:0}



