.label{color:#2c3642;cursor:pointer;display:block;font-size:12px;margin-bottom:2px;text-transform:uppercase;font-weight:bold}.label--inline{vertical-align:middle;display:inline-block}.input{width:100%;padding:10px 10px;box-sizing:border-box;font-size:13px;border:none;border-radius:5px;color:#6f828c}.input:focus{outline:none}.accordion{position:relative}.accordion__title{background:#287bbb;color:#fff;cursor:pointer;font-size:13px;font-weight:600;padding:27px 20px 23px 25px;text-transform:uppercase;margin:0 0 2px}.accordion__title:hover{background:#3285c5}.accordion__title:hover ~ img{opacity:.5}.accordion__title:before{content:"+ ";display:inline-block;width:12px}.accordion__content{padding:20px 30px 30px}.accordion__delete{position:absolute;right:25px;top:23px;opacity:0;cursor:pointer;-webkit-transition:opacity 0.5s;transition:opacity 0.5s}.accordion__delete:hover{opacity:1}.accordion--opened>.accordion__title:before{content:"- "}.accordion .accordion .accordion__content{padding:20px 0 30px}.accordion .accordion .accordion__title{background:none;padding:5px 0;color:#287bbb}



