.button{display:block;background:#287bbb;color:#fff;border:0;padding:8px 20px 8px;font-size:12px;font-weight:600;text-transform:uppercase;cursor:pointer;border-radius:5px;padding-top:18px;padding-bottom:15px;text-decoration:none;width:100%;outline:none}.button:disabled{background:#dddddd;cursor:default}.newsletter-item{color:#666;padding:0;margin:0px -30px;cursor:pointer;position:relative;overflow:hidden;height:66px}.newsletter-item:last-child{border-bottom:none}.newsletter-item:hover{background:rgba(40,123,187,0.1)}.newsletter-item:hover .newsletter-item__delete{display:block}.newsletter-item__delete{position:absolute;right:35px;top:23px;display:none;opacity:0.4;z-index:100}.newsletter-item__link{*zoom:1;padding:10px 30px 10px;position:relative;-webkit-transition:all .25s;transition:all .25s;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}.newsletter-item__link:before,.newsletter-item__link:after{content:" ";display:table}.newsletter-item__link:after{clear:both}.newsletter-item__link.v-enter,.newsletter-item__link.v-leave{-webkit-transform:translate3d(-400px, 0, 0);transform:translate3d(-400px, 0, 0)}.newsletter-item__image{width:60px;height:46px;background-position:center;background-size:cover;float:left;margin-right:10px}.newsletter-item__name{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;margin:5px 0 0}.newsletter-item__date{font-size:11px;text-transform:uppercase;font-weight:bold;opacity:0.7}



