.delete-confirmation{background:#d62d49;box-sizing:border-box;padding:27px 35px 0px;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0);-webkit-transition:all .25s;transition:all .25s;position:absolute;top:0;left:0;right:0;height:66px}.delete-confirmation.v-enter,.delete-confirmation.v-leave{-webkit-transform:translate3d(400px, 0, 0);transform:translate3d(400px, 0, 0)}.delete-confirmation span{color:#fff;font-size:12px;text-transform:uppercase}.delete-confirmation span:hover{text-decoration:underline}.delete-confirmation__cancel{float:right}.delete-confirmation__delete{float:left}



